.hero-img {
  background-color: aquamarine;
  border-radius: 50%;
  object-fit: contain;
  transition: all 0.3s ease;
}
.spline {
  position: relative;
  width: 100%;
  padding-top: 133.33%;
  height: auto !important;
}
.spline canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100% !important;
  height: 100% !important;
}
.img-links{
  max-width: 25px !important;
  min-height: 25px !important;
}
.ant {
  background: linear-gradient(270deg, rgba(8,131,149,1) 0%, rgba(7,25,82,1) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text; /* For compatibility with other browsers */
  color: transparent;    /* Fallback color */
  font-size: clamp(8vw, 78px, 110px);
  line-height: 112px;
  letter-spacing: -4px;
  font-weight: 800;
}
.nextpage{
  animation: moveleft 0.5s alternate infinite both;
}
@keyframes moveleft {
  0%{

  }
  100%{
    transform: translateX(10px);
  }
}
/* animations */
.name {
  -webkit-animation: tracking-in-expand 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
	        animation: tracking-in-expand 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
  animation-delay: calc(var(--delay) * 0.3s);
}
@-webkit-keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}

